.product-item {
    $root: &;
    padding: 80px 0;

    @media (max-width: 916px) {
        padding: 60px 0;
    }

    @media (max-width: 540px) {
        padding: 40px 0;
    }

    &__container {
        display: flex;
        align-items: start;
        justify-content: space-between;
    }

    &_left {
        #{$root} {
            &__img-mobile {
                float: left;
                margin: 0 19px 10px 10px;
            }

            &__container {
                flex-flow: row-reverse;
            }

            &__img {
                margin-right: 150px;
                margin-left: 40px;
            }
        }
    }

    &__title {
        text-align: left;
        display: inline-block;
        font-size: 52px;
        width: auto;

        @media (max-width: 916px) {
            width: auto;
            font-size: 32px;
            margin-bottom: 12px;
        }

        @media (max-width: 360px) {
            font-size: 14px;
        }

        &:after {
            left: 10%;
            width: 100%;
        }
    }

    &__desc {
        font-size: 30px;
        font-weight: 400;
        margin-top: 40px;
        margin-bottom: 45px;

        @media (max-width: 916px) {
            font-size: 18px;
            margin-top: 18px;
            margin-bottom: 18px;
            line-height: 1.2em;
        }

        @media (max-width: 540px) {
            font-size: 12px;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__img {
        margin-right: 40px;
        margin-left: 150px;
        flex: none;
        max-width: 360px;

        @media (max-width: 1300px) {
            display: none;
        }
    }

    &__img-mobile {
        display: none;
        float: right;
        margin: 0 10px 10px 19px;

        @media (max-width: 1300px) {
            display: block;
            max-width: 250px;
        }

        @media (max-width: 916px) {
            max-width: 190px;
        }

        @media (max-width: 540px) {
            max-width: 105px;
            margin-bottom: 10px;
        }

        @media (max-width: 360px) {
            max-width: 85px;
        }
    }

    &__btn {
        /* stylelint-disable */
        font-family: "Oswald", "Open Sans", sans-serif;
        /* stylelint-enable */
        box-sizing: border-box;
        display: flex;
        align-items: center;
        position: relative;
        z-index: 2;
        width: 220px;
        min-height: 95px;
        padding: 5px 24px;
        text-align: center;
        color: #fff;
        font-weight: 600;
        font-size: 20px;
        line-height: 1.2em;
        text-decoration: none;
        text-transform: uppercase;

        @media (max-width: 916px) {
            width: 160px;
            min-height: 65px;
            font-size: 12px;
            line-height: 1.2em;
        }

        @media (max-width: 620px) {
            width: 130px;
            font-size: 10px;
        }

        @media (max-width: 540px) {
            width: 85px;
            min-height: 35px;
            font-size: 8px;
            padding: 5px 10px;
        }

        @media (max-width: 360px) {
            display: flex;
            justify-content: center;
            width: 100%;
            min-height: 0;
            padding: 10px;
        }
    }

    &__btn-box {
        display: inline-block;
        position: relative;
        margin-right: 50px;

        @media (max-width: 1450px) {
            margin-right: 10px;
            margin-bottom: 5px;
        }

        @media (max-width: 1300px) {
            margin-right: 0;
            margin-bottom: 10px;
        }

        @media (max-width: 360px) {
            display: block;
            margin-right: 0;
        }

        &:last-child {
            margin-right: 0;
        }

        &:after {
            content: "";
            display: block;
            position: absolute;
            z-index: 1;
            background-color: #0a6d24;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            -webkit-transform: skew(-24deg);
            -moz-transform: skew(-24deg);
            -o-transform: skew(-24deg);
        }
    }

    &__btn-list {
        box-sizing: border-box;
        display: flex;
        flex-flow: row wrap;
        margin-top: 62px;

        @media (max-width: 1300px) {
            justify-content: space-between;
            width: 100%;
            padding: 0 25px;
            margin-top: 19px;
            padding-top: 20px;
        }

        @media (max-width: 540px) {
            margin-top: 20px;
            padding: 0 10px;
        }

        @media (max-width: 360px) {
            display: block;
        }
    }
}
