.news-item {
    position: relative;
    width: 100%;
    max-width: 426px;
    background-color: $basic-white;

    @media (max-width: 1340px) {
        width: 100%;
        max-width: 100%;
    }

    &__img {
        display: block;
        width: 100%;
        height: auto;
    }

    &__header-container {
        padding: 20px 50px 20px 30px;

        @media (max-width: $tablet-width) {
            padding: 15px 40px 10px 15px;
        }
    }

    &__header {
        margin-bottom: 12px;
        font-family: $second-font;
        font-size: 21px;
        font-weight: 500;
        line-height: 1.14em;
        text-transform: uppercase;

        @media (max-width: $tablet-width) {
            font-size: 15px;
        }
    }

    &__description {
        margin: 0;
        font-size: 16px;
        font-weight: 400;

        @media (max-width: $tablet-width) {
            font-size: 12px;
        }
    }

    &__link {
        display: block;
        position: relative;
        margin: 0;
        padding: 15px 0 15px 30px;
        cursor: pointer;
        background-color: #f3f4f4;
        font-family: $second-font;
        font-size: 19px;
        font-weight: 500;

        @media (max-width: $tablet-width) {
            padding: 10px 0 10px 10px;
            font-size: 14px;
        }

        &:hover {
            opacity: 0.9;
        }

        &::after {
            content: "";
            display: block;
            position: absolute;
            z-index: 3;
            top: 10px;
            right: 70px;
            width: 32px;
            height: 32px;
            background-image: url("../../img/news-arrow-white@2x.png");
            background-repeat: no-repeat;

            @media (max-width: $tablet-width) {
                top: 10px;
                right: 41px;
                background-image: url("../../img/news-arrow-white-mobile@2x.png");
                background-size: 32px 32px;
            }
        }

        &::before {
            content: "";
            display: block;
            position: absolute;
            right: 20px;
            bottom: -10px;
            width: 180px;
            height: 71px;
            opacity: 0.9;
            background-image: url("../../img/green-button.png");
            background-size: 180px 71px;
            background-repeat: no-repeat;

            @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
                background-image: url("../../img/green-button@2x.png");
            }

            @media (max-width: $tablet-width) {
                right: -37px;
                bottom: -28px;
                background-image: url("../../img/green-button-mobile.png");
                background-size: 131px 52px;

                @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
                    background-image: url("../../img/green-button-mobile@2x.png");
                }
            }
        }
    }
}
