.news {
    &__container {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        position: relative;

        @media (max-width: 1340px) {
            flex-wrap: wrap;
        }

        @media (max-width: $tablet-width) {
            flex-direction: column;
        }
    }

    &__list {
        margin-right: 70px;

        @media (max-width: 1340px) {
            margin-right: 0;
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-column-gap: 70px;
        }

        @media (max-width: 900px) {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
        }

        &:last-child {
            margin-right: 0;

            @media (max-width: 900px) {
                display: none;
            }
        }

        &_middle {
            margin-top: 150px;

            @media (max-width: 1340px) {
                margin-top: 0;
            }
        }
    }

    &__article-content {
        margin-bottom: 135px;

        @media (max-width: 1340px) {
            margin-bottom: 70px;
        }

        @media (max-width: $tablet-width) {
            margin-bottom: 60px;
        }
    }
}
